/* stylelint-disable max-line-length */
.filter-container {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  border: 2px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  font-family: Poppins , sans-serif;
  font-weight: 600;
  width: 90%;
}

.filters {
  width: 100%;
}

.filter-name-container {
  border: 2px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 90%;
}

.filter-container p {
  color: white;
  margin: 0;
  padding: 10px;
  text-align: center;
}

.filter-name {
  background-color: black;
  border: 2px solid white;
  border-radius: 10px;
  color: white;
  margin: 0 auto 10px;
  outline-color: yellow;
  padding: 5px;
  width: 90%;
}

.filter-name::placeholder {
  color: rgb(138, 136, 136);
}

.filter-name::selection {
  background: white;
  color: black;
}

.filter-column-sort-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 90%;
}

.filter-column-container, .filter-sort-container {
  border: 2px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  width: 100%;
}

.select-container {
  margin: 0 auto;
  width: 90%;
}

.select-control {
  appearance: none;
  background: black;
  background-image: linear-gradient(45deg, transparent 50%, gray 50%) , linear-gradient(135deg, gray 50%, transparent 50%) , linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px) , calc(100% - 15px) calc(1em + 2px) , calc(100% - 2.5em) 0.5em;
  background-repeat: no-repeat;
  background-size: 5px 5px , 5px 5px , 1px 1.5em;
  border: 2px solid white;
  border-radius: 10px;
  color: white;
  margin: 0 auto 10px;
  padding: 5px;
  width: 100%;
}

.select-control:focus {
  background-image: linear-gradient(45deg, gray 50%, transparent 50%) , linear-gradient(135deg, transparent 50%, gray 50%) , linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 15px) 1em , calc(100% - 20px) 1em , calc(100% - 2.5em) 0.5em;
  background-repeat: no-repeat;
  background-size: 5px 5px , 5px 5px , 1px 1.5em;
  border-color: white;
  outline: 0;
}

.btn-add-remove {
  background-color: white;
  border: 2px solid gray;
  border-radius: 10px;
  color: black;
  margin: 0 auto 10px;
  padding: 5px;
  width: 90%;
}

.btn-add-remove:hover {
  background-color: black;
  color: white;
}

.radio-form {
  color: white;
  margin: 0 auto 5px;
  width: 90%;
}

.chosen-filters-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
}

.chosen-filters {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding: 5px 0;
  width: 100%;
}

.button-remove {
  background-color: white;
  border: 2px solid gray;
  border-radius: 10px;
  color: black;
  padding: 5px;
}

@media screen and ( min-width : 1024px ) {

  .filter-name-container {
    align-items: center;
    flex-direction: row;
  }

  .filter-name {
    color: white;
    margin: 10px auto;
  }

  .filter-column-sort-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .filter-column-container, .filter-sort-container {
    width: 47%;
  }
}

@media screen and ( min-width : 1500px ) {

  .filters {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .filter-name-container {
    flex-direction: column;
    width: 48%;
  }

  .filter-column-sort-container {
    width: 48%;
  }


}
